        width: 80vw;
        height: auto;
        margin-top: 30px;
    }

    .hobbi-box:last-child {
        margin-bottom: 100px;
    }

    .hobbiImg {
        filter: brightness(0.4);
        width: 100%;
        height: 200px;
        object-fit: cover;
    }

   
}